"use client";

import { useState } from "react";
import { Lightbulb } from "lucide-react";
import type { LightControlProps } from "./widget-types";
import { cn } from "@/lib/utils";

export function LightControlWidget({
  title = "Light",
  entity_id,
  state = "off",
  size = "md",
  className,
}: LightControlProps & { className?: string }) {
  const isOn = state === "on";
  const [loading, setLoading] = useState(false);

  async function handleToggle() {
    setLoading(true);
    try {
      const res = await fetch("/api/ha/call-service", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ entity_id, domain: "light", service: "toggle" }),
      });
      if (!res.ok) {
        const data = await res.json().catch(() => ({}));
        console.error("Toggle failed:", data.error);
      }
    } finally {
      setLoading(false);
    }
  }

  const sizeClasses = {
    sm: "h-16 w-16",
    md: "h-20 w-20",
    lg: "h-24 w-24",
  };
  const iconSizes = {
    sm: "h-7 w-7",
    md: "h-8 w-8",
    lg: "h-10 w-10",
  };

  return (
    <button
      type="button"
      onClick={handleToggle}
      disabled={loading}
      className={cn(
        "flex flex-col items-center gap-2 rounded-2xl border-2 transition-all duration-200 active:scale-95 disabled:opacity-70",
        "focus:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 focus-visible:ring-[#4D2FB2]",
        isOn
          ? "border-amber-400/50 bg-amber-100 shadow-lg shadow-amber-200/50 dark:bg-amber-500/20 dark:border-amber-400/30 dark:shadow-amber-500/20"
          : "border-gray-200 bg-gray-50 dark:border-white/15 dark:bg-white/5",
        size === "sm" && "p-3",
        size === "md" && "p-4",
        size === "lg" && "p-5",
        className
      )}
      aria-label={isOn ? `Turn off ${title}` : `Turn on ${title}`}
      aria-pressed={isOn}
    >
      <span
        className={cn(
          "flex items-center justify-center rounded-full transition-colors",
          sizeClasses[size],
          isOn
            ? "bg-amber-400/90 text-white shadow-inner dark:bg-amber-400/80"
            : "bg-gray-200/80 text-gray-500 dark:bg-white/10 dark:text-gray-400"
        )}
      >
        <Lightbulb
          className={cn(iconSizes[size], isOn ? "drop-shadow-sm" : "")}
          fill={isOn ? "currentColor" : "none"}
          strokeWidth={isOn ? 1.5 : 1.8}
        />
      </span>
      <span
        className={cn(
          "text-sm font-medium truncate max-w-full px-1",
          isOn ? "text-gray-900 dark:text-white" : "text-gray-600 dark:text-gray-400"
        )}
      >
        {title}
      </span>
    </button>
  );
}
